                }
              },
              data: this.bigtop5Data2
            },
            {
              name: "()",
              type: "line",
              itemStyle: {
                normal: {
                  lineStyle: {
                    width: 2,
                    color: "#ac8cbf"
                  }
                }
              },
              data: this.bigtop5Data1
            }
          ]
        },
        true
      );
    },
    sureBig() {
      this.dialogFormCharts = true;
      if (this.ablede == 0) {
        this.loading2 = true;
      } else {
        this.loading2 = false;
      }
      this.$http
        .post(serverUrl + "report/top5", {
          limits: 200,
          pid: this.bigValue,
          years: this.timeValue
        })
        .then(response => {
          this.bigtop5Name = [];
          this.bigtop5Data1 = [];
          this.bigtop5Data2 = [];
          this.bigtop5Data3 = [];
          this.bigtop5Data4 = [];
          this.bigtop5Data5 = [];
          response.data.data.map((item, index) => {
            this.numData1.num4 += item.ut;
            this.bigtop5Name.push(item.pname);
            this.bigtop5Data1.push(item.codeQuality);
            this.bigtop5Data2.push(item.bugs);
            this.bigtop5Data3.push(item.testBugs);
            this.bigtop5Data5.push(item.manHaur);
            this.bigtop5Data4.push(item.ut);
          });
          setTimeout(() => {
            this.ablede = 1;
            this.loading2 = false;
            this.barGraph();
          }, 1);
        })
        .catch(err => {
          console.log(err, "err");
        });
    },
    way1(data) {
      this.showMyCharts = true;
      this.$http
        .post(serverUrl + "report/top5", data)
        .then(response => {
          this.top5Name = [];
          this.top5Data1 = [];
          this.top5Data2 = [];
          this.top5Data3 = [];
          this.top5Data4 = [];
          this.top5Data5 = [];
          if (response.data.data.length > 0) {
            response.data.data.map((item, index) => {
              this.top5Name.push(item.pname);
              this.numData.num4 += item.ut;
              this.top5Data1.push(item.codeQuality);
              this.top5Data2.push(item.bugs);
              this.top5Data3.push(item.testBugs);
              this.top5Data5.push(item.manHaur);
              this.top5Data4.push(item.ut);
            });
          } else {
            this.showMyCharts = false;
            this.top5Name = [""];
            this.top5Data1 = [];
            this.top5Data2 = [];
            this.top5Data3 = [];
            this.top5Data4 = [];
            this.top5Data5 = [];
            this.$message({
              duration: 500,
              message: "+"
            });
          }
          this.drawLine();
        })
        .catch(err => {
          console.log(err, "err");
        });
    },
    way2(data) {
      this.showMyChart = true;
      this.$http
        .post(serverUrl + "report/progress", data)
        .then(response => {
          if (response.data.code == 204) {
            this.showMyChart = false;
            this.$message({
              duration: 1000,
              message: ""
            });
            this.roundData4 = 0;
            this.roundData3 = 0;
            this.roundData2 = 0;
            this.roundData1 = 0;
            this.roundData = 0;
            this.color1 = 0;
            this.color2 = 0;
            this.color3 = 0;
            this.color4 = 0;
            this.tabColor();
          } else {
            this.roundData4 = response.data.data.delayDeveloping;
            this.roundData3 = response.data.data.delayReleased;
            this.roundData2 = response.data.data.developing;
            this.roundData1 = response.data.data.released;
            let num =
              this.roundData1 +
              this.roundData2 +
              this.roundData3 +
              this.roundData4;
            this.roundData = (
              (this.roundData1 + this.roundData2) /
              num *
              100
            ).toFixed(0);
            this.color1 = Number((this.roundData1 / num).toFixed(1));
            this.color2 =
              this.color1 + Number((this.roundData2 / num).toFixed(1));
            this.color3 =
              this.color2 + Number((this.roundData3 / num).toFixed(1));
            this.color4 =
              this.color3 + Number((this.roundData4 / num).toFixed(1));
            this.tabColor();
          }
          this.drawRound();
        })
        .catch(err => {
          console.log(err, "err");
        });
    }
  },
  mounted() {
    const _this = this;
    setTimeout(() => {
      _this.$router.push("/");
    }, 900000);
    if (this.$store.state.count) {
      this.$http
        .post(serverUrl + "base/project", {})
        .then(response => {
          response.data.data.map((item, index) => {
            if (item.type === 1) {
              this.optionsBig.push({
                value: item.pid,
                label: item.pname
              });
            }
          });
        })
        .catch(err => {
          console.log(err, "err");
        });
      this.way1({});
      this.way2({});
    } else {
      this.$router.push("/");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="scss" scoped="" type="text/css">
$color: white;
$height: 8rem /* 600/75 */;
.header {
  display: flex;
  padding: 1.5% 1rem /* 100/75 */ 1.5% 1.333333rem /* 100/75 */;
  justify-content: space-between;
  background: #222222;
  & .el-row {
    width: 100%;
    & .pull-right {
      text-align: right;
    }
    & .header-title {
      color: $color;
      font-size: 0.6rem /* 45/75 */;
    }
    & div {
      & div {
        display: inline-block;
        position: relative;

        & .header-input {
          // width: 4rem /* 300/75 */;
          // height: 1.066667rem /* 80/75 */;
          background: #393939;
          font-size: 0.213333rem /* 16/75 */;
          border: 1px solid #2a2a2a;
          color: white;
        }
        & i {
          position: absolute;
          right: 10px;
          top: 16px;
          color: $color;
        }
        & a {
          margin-left: 0.133333rem /* 10/75 */;
        }
      }
      & .header-search {
        width: 1.57rem /* 118/75 */;
        height: 0.666667rem /* 50/75 */;
        font-size: 0.19rem /* 14/75 */;
        line-height: 0.15rem;
        padding: 0;
        color: white;
        margin-left: 0.266667rem /* 20/75 */;
      }
    }
  }
}
.body {
  text-align: center;
  padding: 1.5% 1.33rem /* 100/75 */;
  background: #f2f2f2;
  font-size: 0.373333rem /* 28/75 */;
  color: #666666;
  text-align: left;
  & .el-col-3 {
    text-align: right;
  }
  & .body-sure {
    width: 1.52rem /* 114/75 */;
    height: 0.733333rem /* 55/75 */;
    line-height: 3px;
    font-size: 0.213333rem /* 16/75 */;
    background: #fdaa29;
  }
  & .body-selected {
    width: 4rem /* 300/75 */;
    font-size: 0.133333rem /* 10/75 */ !important;
    margin-left: 0.13rem /* 10/75 */;
    & popper-option {
      width: 2.666667rem /* 200/75 */;
      font-size: 0.133333rem /* 10/75 */;
    }
  }
}
.echarts {
  padding: 1% 1.33rem /* 100/75 */;
  position: relative;
  & #myChart {
    height: $height;
  }
  & #myCharts {
    height: 9.6rem /* 720/75 */;
  }
  & .btn {
    position: absolute;
    right: 0;
    z-index: 100;
    font-size: 0.333333rem /* 25/75 */;
  }
}
.pageNation {
  text-align: center;
  margin-top: 0.133333rem /* 10/75 */;
}
</style>



